input {
  all: unset;
}

body {
  background: url(https://assets.cdn.filesafe.space/6ltD4tW4ej1bPro4eqCz/media/62c1ed2ebc9ee196b6afb127.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  overflow: hidden;
}

.react-pdf__Page__canvas {
  width: 100% !important;
  height: 100% !important;
}

@keyframes wiggle {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

.wiggle {
  animation: wiggle 2.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
}
